@import "master";

@media (min-width: 993px) {
  .product-card {
    &-wrap {
      &:hover {
        padding: 20px;
        margin-top: -20px;

        position: absolute;
        z-index: 15;
        box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
        // width: calc(100% + 20px);
        // margin-left: -10px;
        // padding-left: 25px;
        // padding-right: 25px;
        .product-card__attributes {
          display: block;
          animation: productAttributesAnimation 500ms;
        }
        .product-card__actions-panel {
          display: block;
        }
      }
    }
  }
}

@media (min-width: 769px) {
  .mrp-md-30 {
    margin-right: 30px !important;
  }
}

@media (max-width: 1360px) {
  .main-header {
    &__top {
      &-content {
        &-center {
          width: 300px;
          .main-header__small-bar {
            display: flex;
          }
          .navbar-nav {
            white-space: nowrap;
          }
          .header-osb-fix {
            display: none !important;
          }
        }
        &-right {
          width: 100%;
          .main-header__small-bar {
            margin-left: 28px;
          }
        }
      }
    }
    &__logo {
      &-wrap {
        width: 175px;
      }
    }
  }

  .main-container {
    &__content {
      &-wrap {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
}

@media (max-width: 1200px) {
  .products-grid {
    &__item {
      width: 33%;
    }
  }
}

@media (max-width: 992px) {
  .main-container {
    &__content {
      &-wrap {
        margin-left: -30px;
        margin-right: -30px;
      }
    }
  }

  .product-media-gallery {
    margin-left: -30px;
    margin-right: -30px;

    &__top {
      border-radius: 0 0 30px 30px;
    }
    &__thumbs {
      &-wrap {
        display: none;
      }
    }
  }

  body {
    background-color: #fff;
    background-image: none;
  }

  .main-header {
    &__top {
      &-content {
        &-center {
          width: auto;
        }
        &-right {
          width: 100%;
        }
      }
    }
    &__bottom {
      display: none;
    }

    &__small-bar {
      margin-bottom: 0;
    }
  }

  .main-nav-panel {
    &-wrap {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      z-index: 10;
      background-color: $main;
      padding: 20px 0;
    }
    justify-content: center;

    &__item {
      font-size: 12px;
      &:not(:last-child) {
        margin-right: 60px;
      }

      a {
        & > * {
          display: block;
        }
      }
    }
  }

  .language-switcher {
    display: none !important;
  }

  .applied-filters-panel {
    &__filters {
      display: none;
    }
    &__sort {
      span {
        display: none;
      }
    }
  }

  .applied-filters-panel {
    border-bottom: 1px solid $black-lines;
    padding-bottom: 10px;
    margin-bottom: 20px;
    &__sort {
      justify-content: flex-start;
    }
  }
  .products-grid {
    &__item {
      width: 50%;
      min-height: 0;
    }
  }

  .product-card {
    &:not(.product-card--sm-big) {
      .product-card-wrap {
        padding-right: 0;
      }
      display: flex;
      padding: 0;

      & > div:nth-child(1) {
        width: 50%;
        margin-right: 20px;
        position: relative;
        display: table;
      }
      & > div:nth-child(2) {
        width: 50%;
      }

      .product-card__title {
        font-size: 1rem;
        line-height: 21px;
        font-weight: bold;
        margin-bottom: 0;
      }
      .product-card__label {
        top: auto;
        bottom: 6px;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        text-align: center;
      }
      .product-card__size {
        display: block;
      }
      .product-card__image {
        border: 1px solid $black-lines;
        border-radius: 20px;
      }
      .product-card__rate {
        margin-bottom: 0;
      }
      .product-card__footer {
        .btn {
          display: none;
        }
      }
    }
  }

  .product-page {
    &__description {
      width: 100%;
    }
    &__actions-panel {
      justify-content: space-between;
      &-control-btn {
        span {
          display: none;
        }

        &.product-page__compare-btn {
          display: none;
        }
      }
    }

    &__short-description {
      display: none;
    }

    &__attributes {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      &-item {
        width: 50%;
        margin-bottom: 20px;
      }
    }

    &__reviews {
      flex-direction: column;

      &-summary {
        width: 100%;
        padding-right: 0;

        &-total {
          i {
            font-size: 30px;
            margin-right: 5px;
          }
          &-value {
            font-weight: bold;
            font-size: 18px;
            line-height: 27px;
            margin-left: 20px;
          }
        }

        &-score {
          display: none;
        }
      }
      &-comments {
        width: 100%;
        padding-left: 0;
      }
      &-comment {
        &:not(:last-child) {
          margin-bottom: 30px;
        }
        &-meta {
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          &-rate {
            white-space: nowrap;
            margin-right: 5px;
            i {
              font-size: 10px;
            }
          }
        }
        &-uploads {
          display: flex;
          &.slick-initialized {
            margin-right: -30px;
          }
        }
      }
    }
  }
  .product-media-gallery {
    border-radius: 0 0 30px 30px;
    margin-bottom: 20px;
    &__label {
      top: 20px;
    }
    &__top {
      .swiper-control {
        display: flex;
      }
      border-radius: 0 0 30px 30px;
      .swiper-slide {
        border-radius: 0 0 30px 30px;
      }
    }
    .swiper-container {
      height: 300px;
    }
  }
  .product-spec {
    &:last-child {
      margin-bottom: 10px;
    }
  }

  .product-page-sidebar {
    padding: 0;
    border-radius: 0;
    background: none;
    & > :nth-child(1),
    & > :nth-child(2),
    & > :nth-child(3) {
      display: none;
    }
  }

  .blog-posts-grid {
    &-wrap {
      width: 100%;
    }

    &__item {
      &-wrap {
        width: 100%;

        &:first-child {
          width: 100%;
          .blog-posts-grid__item {
            flex-direction: column;
          }
          .blog-posts-grid__item-thumbnail-wrap {
            width: 100%;
            margin-bottom: 20px;
          }
          .blog-posts-grid__item-content {
            padding-left: 0;
          }
        }
      }
    }
  }

  .cart-table {
    &__item {
      display: grid;
      column-gap: 10px;
      row-gap: 10px;
      grid-template-columns: auto;
      grid-template-rows: auto;
      grid-template-areas:
        "image main"
        "image price"
        "actions quantity"
        "actions total";

      &-weight {
        display: none;
      }
      &-divider {
        display: none;
      }
    }
  }

  .overlay-cdk {
    background: $white;
    padding: 0;
    &__content {
      margin-left: -10px;
      margin-right: -10px;

      &-video {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      &-feed {
        // &-container {
        //   height: 50%;
        //   background: #fff;
        //   border-radius: 30px;
        //   padding: 30px;
        // }
        &-header {
          a {
            display: none;
          }
        }
      }
    }
  }

  .mobile-slider {
    &.slick-initialized {
      margin-left: -30px;
      margin-right: -30px;

      .slick-list {
        padding-right: 30px;
        padding-left: 30px;
      }

      .slick-slide {
        // margin-left: 20px;
        // margin-right: 20px;
        // &:last-child {
        //   margin-left: -20px;
        //   margin-right: 0;
        // }
        // &:first-child {
        //   margin-left: 20px;
        // }
        // &:last-child {
        //   margin-right: 20px;
        // }
      }
    }
  }

  .static-seo {
    &__text {
      max-height: 200px;
      overflow: hidden;
      position: relative;
      margin-bottom: 10px;
      &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 100%;
        background: linear-gradient(
          180deg,
          rgba(255, 255, 255, 0) 0%,
          #ffffff 100%
        );
        height: 100px;
      }
    }

    &.active {
      .static-seo__text {
        max-height: none;
        overflow: initial;
        &::after {
          display: none;
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .main-container {
    &__content {
      padding: 0;
      padding-top: 40px;
      border-radius: 0;
      margin-left: 0;
      margin-right: 0;
    }
  }
}
@media (max-width: 576px) {
  .main-header {
    &__logo-wrap {
      width: 130px;
      flex: 0 0 auto;
    }
    &__top {
      &-content {
        &-center {
          margin: 0;
        }
      }
    }
  }

  .main-container {
    .container {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  .main-footer {
    .container {
      padding-left: 30px;
      padding-right: 30px;
    }

    &__nav-menu {
      display: none;
    }
  }

  .banner-card {
    height: auto;
    &--1 {
      background-position-x: 70%;
    }

    &__title {
      max-width: none;
    }
    &__content {
      max-width: none;
      margin-bottom: 50px;
    }
  }

  .products-grid {
    &__item {
      width: 100%;
    }
  }
}
@media (max-width: 365px) {
  .main-header {
    &__top {
      &-content {
        &-center {
          display: none;
        }
      }
    }
  }
}
